    justify-content: center;
    align-items: center;
    font-family: "BlissPro";
    font-weight: 700;
    margin-bottom: 40px;
    text-transform: uppercase;
}
.hamburger {
    padding: 15px 15px;
    display: none;
    cursor: pointer;
    transition-property: opacity, filter;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    font: inherit;
    color: inherit;
    text-transform: none;
    background-color: transparent;
    border: 0;
    margin: 0;
    overflow: visible;
    &:hover {
        opacity: 0.7;
    }
    &.hamburger--boring {
        &.is-active {
            .hamburger-box {
                .hamburger-inner {
                    transform: rotate(45deg);
                    &::before {
                        top: 0;
                        opacity: 0;
                    }
                    &::after {
                        bottom: 0;
                        transform: rotate(-90deg);
                    }
                }
            }
        }
        .hamburger-box {
            .hamburger-inner,
            .hamburger-inner::before,
            .hamburger-inner::after {
                transition-property: none;
            }
        }
    }
    .hamburger-box {
        width: 40px;
        height: 24px;
        display: inline-block;
        position: relative;
        .hamburger-inner {
            display: block;
            top: 50%;
            margin-top: -2px;
            &::before,
            &::after {
                content: "";
                display: block;
            }
            &::before {
                top: -10px;
            }
            &::after {
                bottom: -10px;
            }
        }
        .hamburger-inner,
        .hamburger-inner::before,
        .hamburger-inner::after {
            width: 40px;
            height: 4px;
            background-color: $purple;
            border-radius: 4px;
            position: absolute;
            transition-property: transform;
            transition-duration: 0.15s;
            transition-timing-function: ease;
        }
    }
}

menu.mobile-menu {
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(#000, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    display: none;
    .hamburger .hamburger-box .hamburger-inner, .hamburger .hamburger-box .hamburger-inner::before, .hamburger .hamburger-box .hamburger-inner::after {
        background: #fff;
    }
    .hamburger {
        position: absolute;
        right: 0;
        top: 0;
    }
    .wrapper {
        text-align: center;
        nav {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 40px;
            a {
                margin: 5px 0;
                padding: 5px 0;
                color: #fff;
                text-decoration: none;
                text-transform: uppercase;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        .phone {
            width: 200px;
            height: 44px;
            border-radius: 25px;
            border: 1px solid #E4E4E4;
            padding: 2px;
            margin: 0 auto;
            display: flex;
            align-items: center;
            color: #180027;
            text-decoration: none;
            font-weight: 500;
            font-family: "BlissPro";
            text-decoration: none;
            background: #fff;
            i {
                display: flex;
                justify-content: center;
                align-items: center;
                flex: none;
                margin-right: 10px;
                width: 38px;
                height: 38px;
                border-radius: 100%;
                background: $purple;
                &::before {
                    content: '';
                    display: block;
                    @include sprite($ic1);
                }
            }
        }
    }
}

header {
    background: #fff;
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.08);
    position: relative;
    z-index: 100;
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    .content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 15px;
        max-width: 1230px;
        a.logo {
            @include sprite($logo);
            flex: none;
            margin-right: 30px;
        }
        nav {
            display: flex;
            a {
                text-decoration: none;
                font-size: 14px;
                color: #838383;
                text-transform: uppercase;
                padding: 30px 15px;
                font-family: "BlissPro";
                font-weight: 600;
                &:hover,
                &.active {
                    color: $purple;
                }
            }
        }
        .right {
            display: flex;
            align-items: center;
            .social {
                display: flex;
                margin: 0 30px;
                a {
                    border-radius: 2px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex: none;
                    margin: 0 2px;
                    border-radius: 100%;
                    transition: .2s ease;
                    &:hover {
                        box-shadow: 0px 0px 5px 0px rgba(#000, 0.1);
                    }
                    &::before {
                        content: '';
                        display: block;
                    }
                    &.vk {
                        &:hover {
                            &::before {
                                @include sprite($vk)
                            }
                        }
                        &::before {
                            @include sprite($vk-g)
                        }
                    }
                    &.wa {
                        &:hover {
                            &::before {
                                @include sprite($wa)
                            }
                        }
                        &::before {
                            @include sprite($wa-g)
                        }
                    }
                    &.viber {
                        &:hover {
                            &::before {
                                @include sprite($viber)
                            }
                        }
                        &::before {
                            @include sprite($viber-g)
                        }
                    }
                }
            }
            .phone {
                width: 200px;
                height: 44px;
                border-radius: 25px;
                border: 1px solid #E4E4E4;
                padding: 2px;
                display: flex;
                align-items: center;
                color: #180027;
                text-decoration: none;
                font-weight: 500;
                font-family: "BlissPro";
                i {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex: none;
                    margin-right: 10px;
                    width: 38px;
                    height: 38px;
                    border-radius: 100%;
                    background: $purple;
                    &::before {
                        content: '';
                        display: block;
                        @include sprite($ic1);
                    }
                }
            }
        }
    }
}



footer {
